ite: white;\n    --text-Color-Gray: gray;\n    --select-Bg: rgba(235, 164, 42, 0.5);\n    --prio-Green: #30b24e;\n    --prio-Yellow: #e1940e;\n    --prio-Red: #ae1a1a;\n    --modal-Textarea-Color: #a7cdde;\n    --dialog-Backdrop: rgba(0, 0, 0, 0.5);\n    --save-Btn-color: rgba(255, 166, 0, 0.85);\n    --box-shadow-Color-Dark: rgba(255, 255, 255, 0);\n    --box-shadow-Color: rgba(175, 255, 255, 0.881);\n    --text-shadow: #427388;\n    --checkbox-border-color: rgba(203, 203, 203, 0.785);\n    --checkbox-checked-color: #e6ab52;\n    --checkbox-hover-color: rgba(255, 229, 182, 0.218);\n    --checkbox-disabled-bg-color: orange;\n}\n/* //layout style  */\n* {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n    font-family: \"MyFont\";\n}\nhtml {\n    background-color: var(--bg-Color);\n}\nbody {\n    color: var(--text-Color-White);\n    height: 98vh;\n}\n#layout-container {\n    width: 120rem;\n    height: 100%;\n    margin: 16px auto;\n    display: grid;\n    grid-template-columns: 2.5fr 10fr 3fr;\n    grid-template-rows: 1fr;\n    background-color: var(--bg-Color);\n    gap: 16px;\n}\n#sidebar-container,\n#task-container,\n#notes-container {\n  border-radius: 16px;\n  background-color: var(--layout-Bg-Color);\n  padding: 0 32px 32px 32px;\n  overflow: auto;\n}\n#sidebar-container,\n.task-header-container,\n.notes-header {\n  position: sticky;\n  display: flex;\n  flex-direction: column;\n  top: 0;\n  background-color: var(--layout-Bg-Color);\n  padding-top: 32px;\n  z-index: 1;\n}\n/* layout of the side bar */\n#sidebar-container {\n    display: flex;\n    flex-direction: column;\n    text-align-last: left;\n}\n#app-logo {\n    color: var(--logo-Main-Color);\n    border-bottom: 1px var(--logo-Main-Color) solid;\n    padding-bottom: 4px;\n}\n#task-filter {\n    border-bottom: 1px var(--logo-Main-Color) solid;\n    margin-top: 32px;\n    margin-bottom: 16px;\n    padding-bottom: 32px;\n}\n\n.for-icon,\n.btn,\n.btn-field,\n#add-task-btn,\n.add-note-btn,\n.add-project-btn,\n.edit-btn,\n.delete-btn,\n.close-btn,\n.conf-delete-btn,\n.conf-delete-note-btn,\n.conf-delete-project-btn,\n.conf-cancel-btn,\n.conf-cancel-note-btn,\n.conf-cancel-project-btn,\n.prio-span,\n.save-btn {\n  cursor: pointer;\n}\n\n.for-icon {\n    width: 25px;\n    height: 25px;\n    background-repeat: no-repeat;\n    background-size: contain;\n    border: none;\n}\n.btn-all-tasks > span {\n    background-image: url(\"./Assets/icon/checklist3.png\");\n}\n.btn-today-tasks > span {\n    background-image: url(\"./Assets/icon/today.png\");\n}\n.btn-week-tasks > span {\n    background-image: url(\"./Assets/icon/timeline.png\");\n}\n.btn-overdue-tasks > span {\n    background-image: url(\"./Assets/icon/overdue.png\");\n}\n.btn-done-tasks > span {\n    background-image: url(\"./Assets/icon/today.png\");\n}\n.btn,\n.btn-field {\n  border-radius: 8px;\n  border: none;\n}\n\n.btn {\n    display: flex;\n    flex-direction: row;\n    font-size: 1.125rem;\n    gap: 12px;\n    line-height: 42px;\n    align-items: center;\n    padding-left: 8px;\n    background: var(--layout-Bg-Color);\n    color: var(--text-Color-White);\n}\n.btn-field {\n    display: grid;\n    grid-template-columns: 8fr 1fr;\n    width: 100%;\n    min-height: 80px;\n    padding: 12px 16px;\n    background-color: var(--bg-Color);\n    color: var(--text-Color-White);\n}\n.btn-field:hover {\n    .edit-project-btn,\n    .delete-project-btn {\n      display: block;\n    }\n}\n\n.current {\n    background-color: var(--select-Bg);\n    width: 100%;\n}\n/* project layout  */\n.project-container {\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    gap: 0.5rem;\n}\n.default-project {\n    margin-top: 0.5rem;\n    width: 100%;\n    border-radius: 8px;\n}\n.project-title {\n    font-size: 1rem;\n    margin-bottom: 14px;\n    font-weight: bold;\n}\n.project-description {\n    font-size: 0.875rem;\n    font-style: italic;\n    grid-row: 2/3;\n    text-align: left;\n    padding-right: 8px;\n}\n.add-new-project {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 8px;\n    margin: 16px auto;\n    justify-content: space-between;\n}\n/* task layout */\n#task-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 50px;\n}\n.task-list-header {\n    background: var(--bg-Color);\n    font-size: 1.125rem;\n    border-radius: 8px;\n}\n#task-list {\n    display: flex;\n    flex-direction: column;\n    padding-top: 7px;\n    gap: 5px;\n}\n.task {\n    display: grid;\n    grid-template-columns: 1fr 1fr 14fr 4fr 1.5fr 1.5fr;\n    gap: 8px;\n    align-items: center;\n    justify-items: center;\n    padding: 14px;\n}\n.new-task {\n    background-color: var(--select-Bg);\n}\n#task-list > .task {\n    border-bottom: 1px solid var(--select-Bg);\n}\n#task-list > .task:nth-child(2) {\n    border-top-left-radius: 8px;\n    border-top-right-radius: 8px;\n    border-top: 1px solid var(--select-Bg);\n}\n#task-list > .task:last-child {\n    border-bottom-left-radius: 8px;\n    border-bottom-right-radius: 8px;\n}\n.task-name {\n    justify-self: left;\n}\n.dot {\n    height: 20px;\n    width: 20px;\n    border-radius: 50%;\n    display: inline-block;\n}\n.dot-green {\n    background-color: var(--prio-Green);\n}\n  \n.dot-yellow {\n    background-color: var(--prio-Yellow);\n}\n  \n.dot-red {\n    background-color: var(--prio-Red);\n}\n.checkbox-container {\n    height: 25px;\n    width: 25px;\n}\ninput[type=\"checkbox\"] {\n    box-sizing: border-box;\n    width: 25px;\n    height: 25px;\n    border: 2px solid var(--checkbox-border-color);\n    border-radius: 4px;\n    appearance: none;\n    background-color: transparent;\n    outline: none;\n    transition: outline 0.1s;\n}\ninput[type=\"checkbox\"]:checked {\n    background-size: cover;\n    border: 1px solid;\n    padding: 4px;\n}\n  \ninput[type=\"checkbox\"]:not(:disabled):checked {\n    border-color: var(--checkbox-checked-color);\n    background-color: var(--checkbox-disabled-bg-color);\n}\n  \ninput[type=\"checkbox\"]:disabled {\n    background-color: var(--checkbox-disabled-bg-color);\n}\n  \n  input[type=\"checkbox\"]:disabled:checked {\n    background-image: var(--checkbox-checked-color);\n}\n@media (hover: hover) {\n    input[type=\"checkbox\"]:not(:disabled):hover {\n      background-color: var(--checkbox-hover-color);\n      transform: scale(1);\n    }\n}\n.completed {\n    opacity: 0.28;\n    background-color: var(--layout-Bg-Color);\n}\n/* tasks list when no tasks */\n.no-task-img {\n    display: flex;\n    background-image: url(\"./Assets/icon/work-order.png\");\n    width: 256px;\n    height: 256px;\n    margin: 56px auto;\n    background-repeat: no-repeat;\n    background-size: contain;\n    background-position: center;\n    opacity: 0.5;\n}\n.no-tasks-text {\n    text-align: center;\n    color: var(--checkbox-border-color);\n    font-style: italic;\n}\n#no-task-container:not(:only-child) {\n    display: none;\n}\n/* note layout */\n#notes-container {\n    display: flex;\n    flex-direction: column;\n}\n#all-note-container {\n    display: flex;\n    flex-direction: column;\n    height: auto;\n    gap: 8px;\n}  \n.note-text-container {\n    grid-row: 1/3;\n    padding: 6px 12px 8px 0;\n}  \n.new-note,\n.default-note {\n  display: grid;\n  grid-template-columns: 90% 10%;\n  grid-template-rows: 45px auto;\n  padding: 10px 6px 10px 16px;\n  overflow-wrap: anywhere;\n  background-color: var(--bg-Color);\n  border-radius: 8px;\n  list-style: none;\n  font-size: 0.938rem;\n} \n.default-note:not(:only-child) {\n    display: none;\n}\n.note-date {\n    grid-column: 1 / 3;\n    color: var(--text-Color-Gray);\n    font-style: italic;\n    font-size: 0.813rem;\n    align-self: flex-end;\n}\n/* modals */\ndialog::backdrop {\n    background-color: var(--dialog-Backdrop);\n    backdrop-filter: blur(2px);\n}\n#project-modal-container,\n.note-modal-container,\n.task-modal-container,\n#delete-conf-modal,\n#delete-conf-note-modal,\n#delete-conf-project-modal {\n  width: 600px;\n  height: fit-content;\n  padding: 8px;\n  background: var(--layout-Bg-Color);\n  color: var(--text-Color-White);\n  border-radius: 16px;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  box-shadow: 0 0 24px var(--box-shadow-Color);\n  font-size: 1.5rem;\n}\n#project-modal-container {\n    border: none;\n}\n  \n.note-modal-container,\n.task-modal-container {\n    position: fixed;\n}\n /* close and save button  */\n.close-btn {\n    width: 28px;\n    height: 28px;\n    padding: 2px 8px;\n    background-color: var(--layout-Bg-Color);\n    color: var(--text-Color-White);\n    font-size: 1.1rem;\n    border: none;\n    float: right;\n}\n.close-btn:hover {\n    text-shadow: 0 0 3px var(--text-Color-White);\n    transition: 0.4ms;\n}\n.save-btn {\n    margin: auto;\n    margin-bottom: 4px;\n    border-style: none;\n    padding: 6px 14px;\n    border-radius: 8px;\n    background-color: var(--save-Btn-color);\n    color: var(--text-Color-White);\n}\n  \n  .save-btn:hover {\n    box-shadow: 0 0 5px var(--logo-Main-Color);\n    transition: 0.4ms;\n}\n.save-btn:active {\n    scale: 1.1;\n}\n  /* modal title */\n.modal-title {\n    font-size: 1.5rem;\n    margin-top: 26px;\n    margin-bottom: 16px;\n    text-align: center;\n}\n  \n.modal-form {\n    height: fit-content;\n    display: flex;\n    flex-direction: column;\n    font-size: 1rem;\n    margin-left: 27px;\n    gap: 12px;\n}\n  \n.modal-textarea,\n.project-title-text {\n    background-color: var(--modal-Textarea-Color);\n    border-style: none;\n    border-radius: 8px;\n    padding: 10px 12px;\n}  \n\n.project-title-text {\n    height: 30px;\n}\n  \n  .modal-textarea {\n    height: 60px;\n}\n  \n.modal-textarea:hover,\n.project-title-text:hover {\n    background-color: var(--modal-Textarea-Color);\n    transition: 0.5ms;\n}\n  \ntextarea:focus,\ninput:focus,\nselect:focus {\n    outline: none;\n}\n  \n#task-text {\n    width: 100%;\n}\n/* task modal */\n#